import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import moment from 'moment';
import {
    HashRouter as Router,
    Route,
    hashHistory
} from 'react-router-dom';
import thunk from 'redux-thunk';
import './GameOver.css';
import App from './App';
class GameOver extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 'balll',
    
        };
        this.qustion1_block = this.qustion1_block.bind(this)
    }
    qustion1_block(dispayBlock) {
        alert('Start new game');
        ReactDOM.render(<App start={Date.now()} />, document.getElementById('root'));
    }
    render() {
  
        return (
            <div className='App3'>
                <div className="Container_fin">
                    <span className='GameOver' onClick={this.qustion1_block}>{this.props.item} {this.qustion1_block}</span>
                </div>
            </div>

        );
    }
}

export default GameOver ;



