import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import moment from 'moment';
import {
    HashRouter as Router,
    Route,
    hashHistory
} from 'react-router-dom';
import thunk from 'redux-thunk';
import registerServiceWorker from './registerServiceWorker';
import App from './App';
import level from './music/level.mp3';
import ReactAudioPlayer from 'react-audio-player';
import './level3.css';
import $ from "jquery";
import Level4 from './level4';
class Level3 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 'ball_level3 ball1',
            value1: 'ball_level3 ball2',
            value2: 'ball_level3 ball3',
            value3: 'ball_level3 ball4',
            value4: 'ball_level3 ball5',
            value5: '',
            value6: '',
            value7: 'ball_level3 ball5',
            value8: 'ball_level3',
            elapsed: 0
        };
        this.qustion1_block = this.qustion1_block.bind(this)
        this.qustion2_block = this.qustion2_block.bind(this)
        this.qustion3_block = this.qustion3_block.bind(this)
        this.qustion4_block = this.qustion4_block.bind(this)
        this.qustion5_block = this.qustion5_block.bind(this)
        this.qustion6_block = this.qustion6_block.bind(this)
        this.qustion7_block = this.qustion7_block.bind(this)
        this.qustion8_block = this.qustion8_block.bind(this)

    }
    componentDidMount() {
        this.timer = setInterval(() => this.tick(), 100);
    }
    componentWillUnmount() {
        clearInterval(this.timer);
    }

    tick() {
        this.setState({
            elapsed: new Date() - this.props.start
        });
    }
    qustion1_block(dispayBlock) {
        this.setState({ value6: 'Tap' })
        this.setState({ value: 'MPS_level3 ball1' })
    }
    qustion2_block(dispayBlock) {

        this.setState({ value1: 'MPS_level3 ball2' })
    }
    qustion3_block(dispayBlock) {
        this.setState({ value6: 'Tap' })
        this.setState({ value2: 'MPS_level3 ball3' })
    }
    qustion4_block(dispayBlock) {

        this.setState({ value3: 'MPS_level3 ball4' })
    }
    qustion5_block(dispayBlock) {

        this.setState({ value4: 'MPS_level3 ball5' })
        this.setState({ value6: 'Tap' })
    }
    qustion6_block(dispayBlock) {

        this.setState({ value5: 'f' })

    }
    qustion7_block(dispayBlock) {

        this.setState({ value7: 'MPS_level3 ball5' })
        this.setState({ value6: '' })
    }

    qustion8_block(dispayBlock) {

        this.setState({ value8: 'MPS_level3' })

    }



    render() {
        const ball1 = this.state.value;
        const ball2 = this.state.value1;
        const ball3 = this.state.value2;
        const ball4 = this.state.value3;
        const ball5 = this.state.value4;
        const ball6 = this.state.value5;
        const ball7 = this.state.value7;
        const ball8 = this.state.value8;
        const elapsed = Math.round(this.state.elapsed / 100);
        const seconds = (elapsed / 10).toFixed(1);
        const gameStart = [ball1, ball2, ball3, ball4, ball5, ball7, ball6]
        const gameOver = ['MPS_level3 ball1', 'MPS_level3 ball2', 'MPS_level3 ball3', 'MPS_level3 ball4', 'MPS_level3 ball5', 'MPS_level3 ball5', '']
        const GameOver_Result = [this.props.item, ''];


$(document).ready(function(){
    $('.Container').css({'align-items': 'flex-start'})
})
        function deepEquals(gameStart, gameOver) {
            if (gameOver === undefined && gameStart !== undefined || typeof gameStart !== typeof gameOver) return false;
            if (gameStart instanceof Object) {
                if (gameStart instanceof Array) {
                    if (!(gameOver instanceof Array) || gameStart.length !== gameOver.length) return false;
                    for (var i = 0; i < gameStart.length; i++) if (!deepEquals(gameStart[i], gameOver[i])) return false;
                } else {
                    if (gameOver instanceof Array || !deepEquals(Object.keys(gameStart).sort(), Object.keys(gameOver).sort())) return false;
                    for (var key in gameStart) if (!deepEquals(gameStart[key], gameOver[key])) return false;
                }
            } else {
                return gameStart === gameOver;
            }
            return true;
        }
        if (deepEquals(gameStart, gameOver)) {
            this.qustion6_block();
            GameOver_Result.push("level3:" + seconds);
            alert('You Win!' + seconds)

            ReactDOM.render(<Level4 item={GameOver_Result} start={Date.now()} />, document.getElementById('root'));

        }


        return (
            <div className='App2'>
                <div className='time'>
                    <p >You time <b>{seconds}seconds </b></p >
                </div>
                <ReactAudioPlayer
                    src={level}
                    autoPlay

                />
                <span>{this.props.item[1]}</span>
                <div className='Container'>
                    <div className={this.state.value} onClick={this.qustion1_block} >
                    </div>
                    <div className={this.state.value7} onClick={this.qustion7_block} >
                    </div>
                    <div className={this.state.value1} onClick={this.qustion2_block}>
                    </div>
                    <div className={this.state.value2} onClick={this.qustion3_block}>
                    </div>
                    <div className={this.state.value3} onClick={this.qustion4_block}>
                    </div>
                    <div className={this.state.value4} onClick={this.qustion5_block}>
                    </div>
                </div>
            </div>
        );
    }
}

export default Level3;



