import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import moment from 'moment';
import {
    HashRouter as Router,
    Route,
    hashHistory
} from 'react-router-dom';
import thunk from 'redux-thunk';
import './Header.css';

class Header extends Component {


    render() {
const ballMassiv=[{
    style:'MPS1'
},
{
    style: 'MPS2'
},
{
        id: 3,
        style: '  '
    }]
        return (
            <div className='App'>
            <div className='Anime'><div className='text'></div></div>
               {ballMassiv.map((dinamicComponent, index)=>
                    <p key={index}>{dinamicComponent.style}</p>)}
            </div>

        );
    }
}

export default Header;



